.wrapper {
    padding: 26px 0 0 8px;
    position: relative;
    &::before {
        content: "";
        position: absolute;
        height: 1px;
        left: 8px;
        right: 8px;
        background: rgba(22, 24, 35, .12);
        transform: scaleY(.5);
        top: 180px;
    }
}

