.wrapper {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    padding: 16px 8px 8px;

    .discover{
        display: flex;
        flex-wrap: wrap;
    }
    .title {
        color: rgba(22, 24, 35, 0.75);
        font-weight: 600;
        font-size: 14px;
        line-height: 20px;
        padding: 16px 0 16px 8px
        }

    .btn {
        display: flex;
        box-sizing: border-box;
        max-width: 100%;
        height: 24px;
        margin-bottom: 12px;
        padding: 3px 10px;
        border: 1px solid rgba(22, 24, 35, 0.2);
        border-radius: 12px;
        transition: background-color 200ms ease-in-out 0s;
        margin-right: 8px;
        margin-left: 8px;
        background-color: transparent;
        min-height: 24px;
        align-items: center;

        &:hover {
            cursor: pointer;
            background-color: rgba(22, 24, 35, 0.03);
        }
    }

    .icon {
        font-size: 1.3rem;
        color: rgb(22, 24, 35);

    }

    .title {
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        color: rgba(22, 24, 35, 0.75);
        font-weight: 700;
        font-size: 14px;
        line-height: 18px;
        padding-left: 6px;
    }

    &::before {
        content: "";
        position: absolute;
        height: 1px;
        left: 8px;
        right: 8px;
        background: rgba(22, 24, 35, .12);
        transform: scaleY(.5);
        top: 365px;
    }  
}